.Background {
  box-sizing: border-box; /* 추가 */
  position: relative;
  width: 100%;
  height: 100%;
  background: #ffb8bc;
  margin: 0;
  padding: 0;
}

.pinkRectangle {
  position: absolute;
  width: 300px;
  height: 326px;
  left: calc(50% - 150px);
  top: calc(50% - 163px + 217.5px);
  background: #FFE9EA;
  border-radius: 40px;
  z-index: 1;
}

.linkContainer {
  display: flex;
  flex-direction: column;
  gap: 20px; /* 간격 조정 */
  align-items: center; /* 버튼들 가운데 정렬 */
  justify-content: center; /* 버튼들 가운데 정렬 */
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.cake2 {
  position: absolute;
  width: 350px;
  height: 350px;
  left: calc(50% - 350px / 2);
  top: calc(35% - 350px / 2 );
}


.title {
  position: absolute;
  width: 327px;
  height: 55px;
  left: 23.5px;
  top: 57px;
}

.partyrule,
.invite,
.gotoParty,
.logout {
  width: 240px;
  height: 54.55px;
  background: #FFE9EA;
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 80%;
  max-width: 400px;
  max-height: 400px;
  background-color: #fff;
  border-radius: 8px;
  z-index: 9999;
}

.overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}

.closeButton {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  padding: 8px 16px;
  background-color: #ff6b6b;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.popupContainer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.popupContent {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.popupImage {
  max-width: 90%;
  max-height: 90%;
}

.popupClose {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  z-index: 1;
}

